import type { CSSObject } from '@emotion/react';
import type { HTMLAttributes } from 'react';
import React from 'react';

import { useDesignSystemTheme } from '../../design-system';
import type { DangerousGeneralProps } from '../../design-system/types';

export interface PreviewCardProps extends DangerousGeneralProps, Omit<HTMLAttributes<HTMLDivElement>, 'title'> {
  icon?: React.ReactNode;
  title?: React.ReactNode;
  subtitle?: React.ReactNode;
  startActions?: React.ReactNode;
  endActions?: React.ReactNode;
  image?: React.ReactNode;
  onClick?: React.MouseEventHandler<HTMLDivElement>;
}

export const PreviewCard = ({
  icon,
  title,
  subtitle,
  children,
  startActions,
  endActions,
  image,
  onClick,
  dangerouslyAppendEmotionCSS,
  ...props
}: PreviewCardProps) => {
  const styles = useResultCardStyles({ onClick });
  const tabIndex = onClick ? 0 : undefined;
  const role = onClick ? 'button' : undefined;

  return (
    <div
      css={[styles['container'], dangerouslyAppendEmotionCSS]}
      tabIndex={tabIndex}
      onClick={onClick}
      role={role}
      {...props}
    >
      {image && <div>{image}</div>}
      <div css={styles['header']}>
        {icon && <div>{icon}</div>}
        <div>
          {title && <div css={styles['title']}>{title}</div>}
          {subtitle && <div>{subtitle}</div>}
        </div>
      </div>
      <div>{children}</div>
      <div css={styles['footer']}>
        <div>{startActions}</div>
        <div>{endActions}</div>
      </div>
    </div>
  );
};

const useResultCardStyles = ({ onClick }: Pick<PreviewCardProps, 'onClick'>): Record<string, CSSObject> => {
  const { theme } = useDesignSystemTheme();
  const isInteractive = onClick !== undefined;

  return {
    container: {
      borderRadius: theme.borders.borderRadiusMd,
      border: `1px solid ${theme.colors.border}`,
      padding: theme.spacing.md,
      color: theme.colors.textSecondary,
      display: 'flex',
      flexDirection: 'column',
      gap: theme.spacing.sm,
      cursor: isInteractive ? 'pointer' : 'default',
      ...(isInteractive && {
        '&:hover, &:focus-within': {
          background: theme.colors.actionTertiaryBackgroundHover,
        },
        '&:active': {
          background: theme.colors.actionTertiaryBackgroundPress,
        },
        '&:focus': {
          outlineColor: theme.colors.actionPrimaryBackgroundDefault,
          outlineWidth: 2,
          outlineOffset: -2,
          outlineStyle: 'solid',
          boxShadow: 'none',
          borderColor: 'transparent',
        },
      }),
    },
    header: {
      display: 'flex',
      alignItems: 'center',
      gap: theme.spacing.sm,
    },
    title: {
      fontWeight: theme.typography.typographyBoldFontWeight,
      color: theme.colors.textPrimary,
    },
    footer: {
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'center',
    },
  };
};
